import { UPDATE_NAME } from '../constants/constants';

const initialState = {
    name: null,
    email: null,
};
export default function userReducer(state = initialState, action) {
    switch (action.type) {
        case UPDATE_NAME:
            return {...state, name: action.payload.name, email: action.payload.email}
        default:
            return state;
    }
}


